<template>
  <div class="cluster author">
    <div>
      <div class="volume-left">
        <el-button type="primary" @click="openDialog('add')">Add User</el-button>
      </div>
      <div class="volume-right">
        <el-input v-model="searchVal" placeholder="" style="width: 255px;"></el-input>
        <el-button type="primary" class="ml5" @click="queryUserList">Search</el-button>
      </div>
    </div>
    <div class="data-block" v-loading="resData.loading">
      <el-table
        :data="resData.resLists"
        class="mt20"
        style="width: 100%">
        <el-table-column type="index" label="#">
        </el-table-column>
        <el-table-column prop="user_id" label="User Name">
        </el-table-column>
        <el-table-column prop="userTypeStr" label="User Type">
        </el-table-column>
        <el-table-column prop="description" label="Comments">
        </el-table-column>
        <el-table-column label="Actions">
          <template slot-scope="scope">
            <el-button type="text" class="text-btn" @click="openDialog('edit', scope.row)">Edit</el-button>
            <el-button type="text" class="text-btn" @click="openDialog('delete', scope.row)">Delete</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="clearfix mt20">
        <el-pagination
          class="fr"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="resData.page.pageSizes"
          :page-size="resData.page.pageSize"
          layout="sizes, prev, pager, next"
          :total="resData.page.totalRecord">
        </el-pagination>
        <span class="fr page-tips pr10">Each page shows</span>
      </div>
    </div>
    <!-- Add New User -->
    <el-dialog
      title="Add New User"
      :visible.sync="addUserDialog"
      width="30%"
      @close="closeDialog">
      <el-form :label-position="labelPosition" ref="addUserForm" :model="addUserForm" :rules="rules">
        <el-form-item label="User Name" prop="userName" :rules="[
            { required: true, message: 'Please enter User Name', trigger: 'blur' },
            { validator: checkUserName, message: 'Consists of letters, numbers and underscores, and must start with a letter. No more than 20 characters.', trigger: 'blur' }
          ]">
          <el-input v-model="addUserForm.userName"></el-input>
        </el-form-item>
        <el-form-item label="Set password" prop="password" :rules="[
            { required: true, message: 'Please enter Password', trigger: 'blur' },
            { validator: checkPassword, message: 'Consists of letters and numbers. No less than 6 characters.No more than 30 characters.', trigger: 'blur' }
          ]">
          <el-input v-model="addUserForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="Confirm password" prop="confirmPassword" :rules="[
            { required: true, message: 'Please enter Confirmation password', trigger: 'blur' },
            { validator: confirmPassword, message: 'Confirmation password is not identical.', trigger: 'blur' }
          ]">
          <el-input v-model="addUserForm.confirmPassword" show-password></el-input>
        </el-form-item>
        <el-form-item label="Type" prop="type">
          <el-select v-model="addUserForm.type" style="width: 100%;">
            <el-option v-for="item in typeList" :value="item.value" :label="item.label" :key="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Comments">
          <el-input type="textarea" v-model="addUserForm.comments"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">No</el-button>
        <el-button type="primary" @click="addUser('addUserForm')">Yes</el-button>
      </span>
    </el-dialog>
    <!-- Edit -->
    <el-dialog
      title="Edit User Authorizaiton"
      :visible.sync="editUserDialog"
      width="30%">
      <el-form :label-position="labelPosition" :model="editUserForm" :rules="rules">
        <el-form-item label="User Name">
          <el-input v-model="editUserForm.user_id" disabled></el-input>
        </el-form-item>
        <el-form-item label="New Password" prop="password" :rules="[
            { validator: checkPassword, message: 'Consists of letters and numbers. No less than 6 characters.No more than 30 characters.', trigger: 'blur' }
          ]">
          <el-input v-model="editUserForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="Confirm New Password" prop="confirmPassword" :rules="[
            { validator: confirmNewPassword, message: 'Confirmation password is not identical.', trigger: 'blur' }
          ]">
          <el-input v-model="editUserForm.confirmPassword" show-password></el-input>
        </el-form-item>
        <el-form-item label="Type">
          <el-select v-model="editUserForm.user_type" style="width: 100%;">
            <el-option v-for="item in typeList" :value="item.value" :label="item.label" :key="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Comments">
          <el-input type="textarea" v-model="editUserForm.description"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editUserDialog = false">No</el-button>
        <el-button type="primary"  @click="updateUser">Yes</el-button>
      </span>
    </el-dialog>
    <!-- Delete -->
    <el-dialog
      title="Delete"
      :visible.sync="deleteUserDialog"
      width="30%">
      <div class="delete-tip">
        <i class="warn"><img src="../assets/images/warn.png" alt=""></i>
        Are you sure you want to delete the User?
      </div>
      <el-form :label-position="labelPosition" :model="editUserForm">
        <el-form-item label="User Name">
          <el-input v-model="editUserForm.user_id" disabled></el-input>
        </el-form-item>
        <el-form-item label="Comments">
          <el-input type="textarea" v-model="editUserForm.description" disabled></el-input>
        </el-form-item>
        <el-form-item label="Type">
          <el-select v-model="editUserForm.userTypeStr" disabled style="width: 100%;">
            <el-option v-for="item in typeList" :value="item.value" :label="item.label" :key="item.value"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteUserDialog = false">No</el-button>
        <el-button type="primary" @click="deleteUser">Yes</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import baseGql from '../graphql/authorization'
export default {
  name: 'authorization',
  data () {
    return {
      labelPosition: 'top',
      searchVal: '',
      resData: {
        loading: true,
        page: {
          pageSizes: [10, 20, 30, 40],
          pageNo: 1,
          pageSize: 10,
          totalRecord: 0,
          totalPage: 1
        },
        resLists: []
      },
      userInfoList: [],
      volumeName: '',
      addUserDialog: false,
      editUserDialog: false,
      deleteUserDialog: false,
      addUserForm: {
        userName: '',
        password: '',
        confirmPassword: '',
        comments: '',
        type: null
      },
      typeList: [
        {
          value: '2',
          label: 'admin'
        },
        {
          value: '3',
          label: 'user'
        }
      ],
      editUserForm: {
        password: '',
        confirmPassword: ''
      },
      rules: {
        type: [
          { required: true, message: 'Please select Type', trigger: 'change' }
        ]
      }
    }
  },
  computed: {
    crumbInfo () {
      return this.volumeName
    }
  },
  methods: {
    handleSizeChange (val) {
      this.resData.page.pageSize = val
      this.resData.page.pageNo = 1
      this.handleCurrentChange(1)
    },
    handleCurrentChange (val) {
      this.resData.page.pageNo = val
      const start = (val - 1) * this.resData.page.pageSize
      const end = val * this.resData.page.pageSize
      this.resData.resLists = this.userInfoList.slice(start, end)
    },
    queryUserList () {
      const that = this
      that.resData.loading = true
      const variables = {
        num: 10000
      }
      this.apollo.query(this.url.authorization, baseGql.queryUserList, variables).then((res) => {
        that.resData.loading = false
        if (res) {
          let finalUserInfo = []
          if (this.searchVal && this.searchVal.trim() !== '') {
            res.data.listUserInfo.forEach(user => {
              if (user.user_id.indexOf(this.searchVal.trim()) > -1) {
                finalUserInfo.push(user)
              }
            })
          } else {
            finalUserInfo = res.data.listUserInfo
          }
          // 按user_type和user_id排列
          finalUserInfo.sort(function (a, b) {
            return a.user_id.charCodeAt(0) - b.user_id.charCodeAt(0)
          }).sort(function (a, b) {
            return a.user_type - b.user_type
          })
          this.userInfoList = finalUserInfo
          this.userInfoList.forEach(item => {
            if (item.user_type === 1) {
              item.userTypeStr = 'root'
            }
            if (item.user_type === 2) {
              item.userTypeStr = 'admin'
            }
            if (item.user_type === 3) {
              item.userTypeStr = 'user'
            }
          })
          this.resData.page.totalRecord = this.userInfoList.length
          this.handleCurrentChange(1)
        } else {
        }
      }).catch(() => {
        that.resData.loading = false
      })
    },
    addUser (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const variables = {
            accessKey: '',
            iD: this.addUserForm.userName,
            password: this.addUserForm.password,
            secretKey: '',
            description: this.addUserForm.comments || '',
            type: parseInt(this.addUserForm.type)
          }
          this.apollo.mutation(this.url.authorization, baseGql.createUser, variables).then((res) => {
            if (res.code === 200) {
              this.queryUserList()
              this.addUserDialog = false
              this.addUserForm = {}
              this.$message({
                message: 'success',
                type: 'success'
              })
            } else {
              this.$message.error(res.message)
            }
          }).catch((error) => {
            this.resData.loading = false
            console.log(error)
          })
        } else {
          return false
        }
      })
    },
    updateUser () {
      if (this.editUserForm.password !== this.editUserForm.confirmPassword) {
        this.$message({
          message: 'Confirmation password is not identical.',
          type: 'warning'
        })
        return
      }
      const variables = {
        accessKey: '',
        secretKey: '',
        password: this.editUserForm.password || '',
        userID: this.editUserForm.user_id,
        description: this.editUserForm.description,
        type: parseInt(this.editUserForm.user_type)
      }
      this.apollo.mutation(this.url.authorization, baseGql.updateUser, variables).then((res) => {
        if (res.code === 200) {
          this.queryUserList()
          this.editUserDialog = false
          this.$message({
            message: 'success',
            type: 'success'
          })
        } else {
          this.$message.error(res.message)
        }
      })
    },
    deleteUser () {
      const variables = {
        userID: this.editUserForm.user_id
      }
      this.apollo.mutation(this.url.authorization, baseGql.deleteUser, variables).then((res) => {
        if (res.code === 200) {
          this.queryUserList()
          this.deleteUserDialog = false
          this.$message({
            message: 'success',
            type: 'success'
          })
        } else {
          this.$message.error(res.message)
        }
      }).catch((error) => {
        this.resData.loading = false
        console.log(error)
      })
    },
    openDialog (tag, row) {
      if (tag === 'add') {
        this.addUserDialog = true
      }
      if (tag === 'edit') {
        this.editUserDialog = true
        this.editUserForm = Object.assign({}, row)
        this.editUserForm.user_type = this.editUserForm.user_type + ''
      }
      if (tag === 'delete') {
        this.deleteUserDialog = true
        this.editUserForm = row
        // console.log(row)
      }
    },
    closeDialog () {
      this.addUserDialog = false
      this.addUserForm = {}
      this.$refs['addUserForm'].resetFields()
    },
    checkUserName (rule, value, callback) {
      if (!(/(^[A-Za-z][A-Za-z0-9_]{0,20}$)/.test(value))) {
        return callback(new Error('Consists of letters, numbers and underscores, and must start with a letter. No more than 20 characters.'))
      } else {
        callback()
      }
    },
    checkPassword (rule, value, callback) {
      if (!(/([A-Za-z0-9]{6,30}$)/.test(value))) {
        return callback(new Error('Consists of letters and numbers. No less than 6 characters.No more than 30 characters.'))
      } else {
        callback()
      }
    },
    confirmPassword (rule, value, callback) {
      if (this.addUserForm.password !== this.addUserForm.confirmPassword) {
        return callback(new Error('Confirmation password is not identical.'))
      } else {
        callback()
      }
    },
    confirmNewPassword (rule, value, callback) {
      if (this.editUserForm.password !== this.editUserForm.confirmPassword) {
        return callback(new Error('Confirmation password is not identical.'))
      } else {
        callback()
      }
    }
  },
  mounted () {
    let query = this.$route.query
    this.volumeName = query.volumeName
    this.queryUserList()
  }
}
</script>

<style scoped>
  .volume-left {
    display: inline-block;
  }
  .volume-right {
    display: inline-block;
    float: right;
  }
  .delete-tip {
    padding-bottom: 20px;
    line-height: 30px;
    text-align: center;
    color: #333;
  }
  .delete-tip .warn img {
    padding-right: 5px;
    width: 20px;
    height: 20px;
  }
</style>
<style>
  .el-form-item__error {
    position: unset !important;
  }
</style>



